//import logo from './logo.svg';
import './App.css';
//import YARA from "./superlogo.png";
import React, { useState ,useEffect} from 'react';
//components
import Vote from './components/vote';


function App() {

  const [voting, setChoise] = useState([]);

  useEffect(() => {
    fetch('/vote/get-Choises')
      .then(r => r.json())
      .then(data => {
        console.log(data)
        setChoise(data.vote);
      })
  }, [])
  return (
    <div>
      <header className="App-header">
        <div className="container-title">
          <h3>בנק הפועלים</h3>
          <span><img className="back" src="data:image/svg+xml;base64," /></span>
        </div>
         <h2>18.3%</h2>
         
        <div className="text">שיעור ההחזקה שלך</div>
        <div className="poalim">תבחר/י מי יהיו הדרקטורים בנק הפועלים</div>
      </header>
       <div className="choose">בחר/י שני דירקטורים רגילים</div>
      <div>
        {voting.map((vote, index) => {

          return (<Vote
            key={index}
            logo={vote.logo}
            name={vote.name}
            dislike={vote.dislike}
            like={vote.like}
            
            
          />)
        })
        }
      </div>
      <div className="center">
      <button>שלח את ההצבעה שלי!</button></div>
    </div>
  );

}

export default App;
